<template>
  <div class="container">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>´

    <b-card class="card">
      <b-row>
        <b-col>
          <h1>Asset Management</h1>
          <hr />
          <b-button v-b-modal.modal-add-asset variant="primary">Add Asset</b-button>
          <hr />
          <b-table striped hover :items="assets" :fields="fields">

            <template #cell(validFrom)="row">
              {{ row.item.validFrom | dateFormat }}
            </template>

            <template #cell(validTo)="row">
              {{ row.item.validTo | dateFormat }}
            </template>

            <template #cell(price)="row">
              ${{ row.item.price | decimalFormat }}
            </template>

            <template #cell(actions)="row">
              <b-button size="sm" class="mr-1" v-b-modal.modal-edit-asset @click="selItem = row.item" variant="success">
                Edit
              </b-button>&nbsp;
              <b-button size="sm" variant="danger" v-b-modal.modal-confirmation @click="selItem = row.item">
                Delete
              </b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>
    </b-card>

    <add-asset-dialog @loadList="getAssets()"></add-asset-dialog>
    <edit-asset-dialog :asset="selItem" @loadList="getAssets()"></edit-asset-dialog>

    <confirmation-dialog @confirm="deleteAsset()" :title="'Confirmation'"
      :content="'Do you want to delete this asset?'">
    </confirmation-dialog>
  </div>
</template>
<script>

import assetApi from '../../api/assetApi';
import addAssetDialog from './AddAssetDialog.vue';
import editAssetDialog from './EditAssetDialog.vue';
import confirmationDialog from '@/common/ConfirmationDialog.vue';
import loading from 'vue-loading-overlay';

export default {
  components: {
    addAssetDialog,
    editAssetDialog,
    confirmationDialog,
    loading
  },
  data: function () {
    return {
      // Note 'isActive' is left out and will not appear in the rendered table
      fields: [
        {
          key: 'id',
          label: 'Id',
          sortable: true
        },
        {
          key: 'name',
          label: 'Name',
          sortable: true
        },
        {
          key: 'validFrom',
          label: 'Valid From',
          sortable: true,
        },
        {
          key: 'validTo',
          label: 'Valid To',
          sortable: true,
        },
        {
          key: 'price',
          label: 'price',
          sortable: true,
        },
        { key: 'actions', label: 'Actions' }
      ],
      assets: [],
      selItem: {},

      isLoading: false
    }
  },
  name: 'asset-list',
  methods: {
    async getAssets() {
      let vm = this;
      const { data } = await assetApi.fetchAssets();
      vm.assets = data;
      console.log(vm.assets)
    },

    editAsset(item) {
      this.selItem = item;

    },

    async deleteAsset() {
      this.isLoading = true;
      try {
        const { data } = await assetApi.deleteAsset(this.selItem.id);
        if (data) {
          this.$toastr.success('Deleted successfully', 'Information');
          this.selItem = {};
          this.getAssets();
        } else {
          this.$toastr.warning('Unable to delete asset', 'Warning');
        }
      } catch (error) {
        this.$toastr.error(error.error.text, 'Error');
      }
      this.isLoading = false;
    }
  },
  async mounted() {
    let vm = this;
    await vm.getAssets()
  }
}

</script>
<style>
.card {
  padding: 15px
}
</style>