<template>
    <div>
        <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>´

        <b-modal id="modal-add-asset" ref="modal" title="Add Asset" ok-title="Save" @ok="handleOk">
            <form>
                <b-row>
                    <b-col col-auto md="3" class="my-auto">
                        <label>Name</label>
                    </b-col>
                    <b-col>
                        <ValidationProvider name="name" rules="required" v-slot="{ errors }">
                            <input type="text" class="form-control" v-model="form.Name"
                                name="name" />
                            <span>{{ errors[0] }}</span>
                        </ValidationProvider>
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col col-auto md="3" class="my-auto">
                        <label>Price</label>
                    </b-col>
                    <b-col>
                        <input type="number" v-model="form.Price" class="form-control" name="Price" />
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col col-auto md="3" class="my-auto">
                        <label>Valid From</label>
                    </b-col>
                    <b-col>
                        <input type="date" v-model="form.ValidFrom" class="form-control" name="ValidFrom" />
                    </b-col>
                </b-row>
                <b-row class="mt-2">
                    <b-col col-auto md="3" class="my-auto">
                        <label>Valid To</label>
                    </b-col>
                    <b-col>
                        <input type="date" v-model="form.ValidTo" class="form-control" name="ValidTo" />
                    </b-col>
                </b-row>
            </form>
        </b-modal>
    </div>
</template>
<script>
import assetApi from '../../api/assetApi';
import loading from 'vue-loading-overlay';

export default {
    components: {
        loading
    },
    name: 'add-asset-dialog',
    data() {
        return {
            form: {
                Name: '',
                Price: 0,
                ValidFrom: null,
                ValidTo: null
            },
            isLoading: false
        }
    },
    methods: {
        async handleOk(bvModalEvent) {
            this.isLoading = true;
            try {
                bvModalEvent.preventDefault();

                const { data } = await assetApi.addAsset(this.form);
                if (data.success) {
                    this.$toastr.success(data.message, 'Information');
                    this.$emit('loadList', data);
                    this.$bvModal.hide('modal-add-asset');
                } else {
                    this.$toastr.warning(data.message, 'Warning');
                }
            } catch (error) {
                this.$toastr.error('Error saving asset', 'Error');
            }
            this.isLoading = false;
        }
    },
}
</script>